import React from 'react';
import styles from './EditorHeader.scss';
import classNames from 'classnames/bind';

const cx = classNames.bind(styles);

const EditorHeader = () => (
  <div className={cx('editor-header')}>
      .
  </div>
);

export default EditorHeader;


