import React, {Component} from 'react';
import styles from './EditorPane.scss';
import classNames from 'classnames/bind';
import Button from 'components/common/Button';

// CodeMirror   CSS 
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/monokai.css';


const cx = classNames.bind(styles);

let CodeMirror = null;
const isBrowser = process.env.APP_ENV === 'browser';
if(isBrowser) {
  CodeMirror = require('codemirror');
  require('codemirror/mode/markdown/markdown');
  require('codemirror/mode/javascript/javascript');
  require('codemirror/mode/jsx/jsx');
  require('codemirror/mode/css/css');
  require('codemirror/mode/shell/shell');
}

class EditorPane extends Component {

  editor = null //  ref
  codeMirror = null // CodeMirror 
  cursor = null //   cursor 

  initializeEditor = () => {
    this.codeMirror = CodeMirror(this.editor, {
      mode: 'markdown',
      theme: 'monokai',
      lineNumbers: true, //   
      lineWrapping: true //      
    });
    this
      .codeMirror
      .on('change', this.handleChangeMarkdown);
  }

  componentDidMount() {
    this.initializeEditor();
  }

  handleChange = (e) => {
    const {onChangeInput} = this.props;
    const {value, name} = e.target;
    onChangeInput({name, value});
  }

  handleChangeMarkdown = (doc) => {
    const {onChangeInput} = this.props;
    this.cursor = doc.getCursor(); //  cursor   
    onChangeInput({
      name: 'markdown',
      value: doc.getValue()
    });
  }

  componentDidUpdate(prevProps, prevState) {
    // markdown    .        ,   
    //    .
    if (prevProps.markdown !== this.props.markdown) {
      const {codeMirror, cursor} = this;
      if (!codeMirror) 
        return; //    
      codeMirror.setValue(this.props.markdown);
      if (!cursor) 
        return; //   
      codeMirror.setCursor(cursor);
    }
  }

  onImageButtonClick = () => {
    this.imagePicker.click();
  }
  render() {
    const {handleChange, onImageButtonClick} = this;
    const {title, onImageUpload, option, price, onPostItem} = this.props;
    
    return (
      <div className={cx('editor-pane')}>
        <input className={cx('title')} placeholder=" " name="title"
          value={title}
          onChange={handleChange}/>
        <div className={cx('code-editor')} ref={ref => this.editor = ref}></div>
        <div className={cx('utils')}>
          <div className={cx('description')}></div>
          <div className={cx('image-button')} onClick={onImageButtonClick}>
             
          </div>
          <input type="file" onChange={onImageUpload} ref={ref=>{this.imagePicker=ref}} name="file" style={{display: "none"}}/>
        </div>
        <div className={cx('utils')}>
          <div className={cx('description')}> </div>
          <input 
            type="text" 
            name="option"
            value={option}
            onChange={handleChange}
            placeholder=" .  ."/>
        </div>
        <div className={cx('utils')}>
          <div className={cx('description')}> </div>
          <input 
            type="text" 
            name="price" 
            value={price}
            onChange={handleChange}
            placeholder=" .  ."/>
        </div>
        <div className={cx('utils')}>
          {/* <div className={cx('description')}></div> */}
          <div className={cx('post-button')} onClick={onPostItem}>
            
          </div>
          {/* <Button onClick={onPostItem}></Button> */}
        </div>
      </div>
    );
  }
}

export default EditorPane;


